*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}


body{
    width: 100%;
    background-color: whitesmoke;
}

#root{
    width: 100%;
    
}

header{
    width: 100%;
    height: 120px;
    background-color: white;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    position: fixed;
    top:0;
    left:0;
    z-index: 100;

}

.top-nav{
    width: 100%;
    background-color: white;
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 70px;
    padding: 0 40px;
}

.top-nav .phone, .mail{
    width: 15%;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    gap:8px;

    
}

.top-nav .mail{
    justify-content: flex-end;
}

.top-nav .phone img,
.mail img {
    width: 17px;
}

.top-nav .phone small,
.mail small {
    font-size: 17px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
}

.top-nav .search{
   width: 70%;
   display: flex;
   justify-content: center;
   align-items: center;
   position: relative;
}
.top-nav .search input{
    width: 60%;
    border: 1px solid grey;
    border-radius: 30px;
    padding: 10px 50px 10px 30px;
    outline: none;
    font-size: 17px;
    transition: .5s ease;
}

.top-nav .search img{
    position: absolute;
    top:50%;
    transform: translateY(-50%);
    right: 23%;
   
}

.main-nav{
    width:100%;
    height: 70px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0px 40px;
    background-color: crimson;
}

.main-nav .logo{
    color:white;
}

.main-nav .links{
    width: 60%;
    display: flex;
    justify-content: center;
    align-items: center;
    gap:25px;
}

.main-nav .links a{
    text-decoration: none;
    color:rgba(255,255,255,0.8)
}

.main-nav .account-links{
    width: 25%;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    gap:10px;
}

.account-links button{
    width: 30%;
    background-color: transparent;
    display: flex;
    justify-content: center;
    align-items: center;
    border:1px solid white;
    color:white;
    padding: 7px 30px;
    border-radius: 10px;
    cursor: pointer;
    transition: .5s ease;

}

.account-links button:hover {
    background-color: white;
    color:#333;
}

main{
    width: 100%;
    height: fit-content;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    /* margin-top:120px ; */
}

.Banner{
    width: 100%;
    height: 500px;
    background: linear-gradient(rgba(0,0,0,0.6), rgba(0,0,0,0.6)),url("./main/bg.png");
    background-size: 100%;
    background-position: center center;
    background-repeat: no-repeat;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center
}

.Banner h1{
    width: 100%;
    text-align: center;
    font-size: 50px;
    color:white;
}

.Banner p{
    width: 100%;
    text-align: center;
    font-size: 25px;
    font-style: italic;
    margin-bottom: 30px;
    color: white;

}

.Banner button{
    width:fit-content;
    padding: 10px 40px;
    color:white;
    background-color: crimson;
    font-size: 18px;
    cursor: pointer;
    border: none;
    border-radius: 15px;
    transition: .3s ease;
}

.Banner button:hover {
    background-color: white;
    color:black;
}


.about-us{
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    padding: 80px;
    gap:40px;
}


.about-us h1{
    font-size: 50px;
    margin: 20px 0;
    font-weight: 100;
    text-align: center;
    width: 100%;
}

.aboutus-container{
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    gap:100px;
    padding: 40px;
}

.aboutus-container img{
    width: 30%;
}

.aboutus-container p{
    width: 60%;
    text-align: justify;
}

/* courses style rules */
.courses-container{
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    padding: 40px;
}

.courses-container .courses-box{
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content:center;
    align-items: flex-start;
    flex-wrap: wrap;
    gap:40px;
    margin-top: 40px;
}

.course-card{
    width: 220px;
    height: 300px;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    gap:15px;
    border:1px solid grey;
    padding: 20px;
    border-radius: 10px;
    cursor: pointer;
    transition: .5s ease;
}

.course-card:hover {
    box-shadow: 0px 8px 16px rgba(0,0,0,0.1);
    border: none;
}

.course-card img{
    height: 40%;
    object-fit: contain;
}

.course-details{
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    gap:8px;
}

.course-details h3{
    width: 100%;
    font-size: 15px;
    text-align: center;
    font-weight: bold;


}

.course-details p{
    font-size: 17px;
    color: darkorange;
    font-weight: 500;
}

.course-card button{
    width: 80%;
    padding: 7px 20px;
    border: 1px solid grey;
    color:black;
    font-weight: bold;
    background-color: transparent;
    border-radius: 10px;
    cursor: pointer;
    transition: .5s ease;
}

.course-card button:hover{
    background-color: crimson;
    color: white !important;
    border: none;
}


footer{
    width: 100%;
    height: 40px;
    background-color: crimson;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 10px 0;
    position: fixed;
    bottom: 0;
}

footer p{
    color:white;
    font-size: 11px;
    font-family: monospace;
}


.add-box{
    width: 200px;
    height: 100px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px 20px;
    background-color: skyblue;
    margin-top: 100px;
    border-radius: 15px;
}
.add-box small{
    font-size: 30px;
    cursor: pointer;
    color: aliceblue;
}
.add-box p{
    font-size: 50px;
    cursor: pointer;
    color: aliceblue;
}
.add-box span{
    
   border-radius: 20px;
   padding: 5px;
   border: 5px solid black;
}
.add-box  p:hover{
    color: crimson;
}
.theme-btn{
    display: flex;
    width: 40px;
    height: 20px;
    background-color: darkorange;
    border-radius: 20px;
    align-self: flex-end;
    position: relative;
    cursor: pointer;
    bottom: 25px;
    left: 0;
    
}
.theme-btn:hover{
    background-color: crimson;
}
.theme-ball{
    width: 20px;
    height: 20px;
    border-radius: 50%;
    background-color: white;
    position: absolute;
    transition: 0.5s;
    
}



form{
    width: 25%;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    gap: 15px;
    background-color: white;
    padding: 30px;
    margin: 0 auto;
}

.title{
    font-size: 30px;
    color: black;
    font-weight: 100;
    width: 100%;
    text-align: left;
}
.input-group{
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    gap: 20px;
}
.input-group input, button{
    width: 100%;
    padding: 10px;
    outline: none;
    border: 1px solid grey;
    font-weight: lighter;
    color: black;
}
.input-group input::placeholder{
    font-weight: 100;
}
.input-group input:focus{
    border: 1px solid crimson;
}
.input-group button{
    border: 1px solid crimson;
    color: grey;
    background-color: transparent;
    font-size: 20px;
    cursor: pointer;
    transition: 2s ease all;
}
.input-group button:hover{
    background-color: crimson;
    color: white;
}

.comment-display {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    gap: 5px;
    border: 0.5px solid black;
    /* background:url("./images/dateme.webp"); */
    padding-left: 10px;
 
}

.input-group textarea{
    width: 100%;
    height: 60px;
    color: rgb(0, 0, 0);
    font-weight: lighter;
}
.input-group textarea:focus{
    border: 1px solid crimson;
    outline: none;
}

