*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}
.meme-app-container{
    width: 50%;
  
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: rgb(255, 255, 255);
}
.meme-container{
    width: 100%;
   
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.top{
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 10px;
   
}
.top span{
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 20px;
    margin-top: 20px;
   
}
.top span input{
    width: 50%;
    height: 40px;   
    outline: none;
    border: 1px solid grey;
    padding-left: 10px;
   
}
.top button{
    width: 100%;
    height: 50px;
    font-size: 20px;
    color: white;
    background-color: darkorange;
    cursor: pointer;
    transition: 1s ease all;
}
.top button:hover{
    background-color: #444;
}
.bottom{
    width: 100%;
    height: 10px;
    position: relative;
}
.bottom img{
    width: 100%;
}
.bottom h1{
    position: absoluteou;
}
.top-text-display{
    width: 800px;
    font-size: 40px;
    overflow: auto;
    -webkit-text-stroke:2px  black;
    
      color: white;
      top: 30px;
      left:10px;
      display: flex;
      justify-content: center;
      align-items: center;

}
.bottom-text-display{
    width: 800px;
    font-size: 40px;
    overflow: auto;
    -webkit-text-stroke:2px  black;
       
      color: white;
      bottom: 550;
      left:30px;
      display: flex;
      justify-content: center;
      align-items: center;

}
