.review-section{
    width: 500px;
    height: 500px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
   margin-top: 40px;
    background-color: aliceblue;
    padding: 20px;
}

.theme-btn{
    display: flex;
    width: 40px;
    height: 20px;
    background-color: darkorange;
    border-radius: 20px;
    align-self: flex-end;
    position: relative;
    cursor: pointer;
    top: -25px;
    left: 0;
}
.theme-btn:hover{
    background-color: crimson;
}
.them-ball{
    width: 20px;
    height: 20px;
    border-radius: 50%;
    background-color: white;
    position: absolute;
    top: 0;
    left: 0;
    transition: 0.5s;
}
.review-card{
    width: 70%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    gap: 30px;
  
}
.review-card p{
    color: grey;
}
